.teachers {
    .container {
        h1 {
            font-family: 'Inter', sans-serif;
            color: #011D42;
            margin: 60px 0px;
        }

        .cards {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            grid-gap: 50px;

            .cardT {
                display: flex;
                justify-content: space-between;
                align-items: center;
                transition: 0.4s;
                border-radius: 10px;
                cursor: pointer;
                width: 100%;
                .textsT {
                    display: flex;
                    .MuiSvgIcon-root{
                        color: #000D29;
                        margin: 5px 10px;
                    }
                    .infoo {
                        margin: 0px 10px;
                        font-family: 'Inter', sans-serif;

                        h3 {
                            margin: 0px;
                            color: #000D29;
                        }

                        p {
                            color: #5D667A;
                        }

                        h4 {
                            color: #000D29;

                            span {
                                color: #5D667A;
                            }
                        }
                    }
                }
            }
            .cardT:hover{
                box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
            }
        }
    }
}

@media screen and (max-width: 330px){
    .teachers {
        .container {
            h1 {
                font-family: 'Inter', sans-serif;
                color: #011D42;
                margin: 60px 0px;
            }
    
            .cards {
                display: grid;
                grid-template-columns: repeat(1, 1fr);
                grid-gap: 30px;
    
                .cardT {
                    display: block;
                    justify-content: space-between;
                    align-items: center;
                    transition: 0.4s;
                    border-radius: 10px;
                    cursor: pointer;
    
                    .textsT {
                        display: flex;
                        margin: 10px 0px;
                        .MuiSvgIcon-root{
                            display: none;
                        }
                        .infoo {
                            margin: 0px 10px;
                            font-family: 'Inter', sans-serif;
    
                            h3 {
                                margin: 0px;
                                color: #000D29;
                            }
    
                            p {
                                color: #5D667A;
                            }
    
                            h4 {
                                color: #000D29;
    
                                span {
                                    color: #5D667A;
                                }
                            }
                        }
                    }
                }
                .cardT:hover{
                    box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
                }
            }
        }
    }
}